<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <!-- 数据表格 -->

    <BasicTable
      @register="registerTable"
      :searchInfo="searchInfo"
      @resize-column="(w, col) => (col.width = w)"
      class="fix-basic-table"
    >
      <template #toolbar>
        <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreateIndex">添加</a-button>
        <a-button
          class="red-btn"
          :disabled="!hasSelected"
          :preIcon="IconEnum.DELETE"
          @click="handleDeleteIndex"
          >删除</a-button
        >
      </template>
      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'action'">
          <TableAction :actions="getWiringAction(record)" />
        </template>
      </template>
    </BasicTable>

    <div v-if="loadingFlag" class="dialog-mask"></div>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import type { FormProps } from '/@/components/Form/src/types/form';
  import { onMounted, reactive, ref, computed } from 'vue';
  import {
    BasicTable,
    useTable,
    TableAction,
    EditRecordRow,
    BasicColumn,
    SorterResult,
  } from '/@/components/Table';
  import { TreeItem } from '/@/components/Tree';
  import { PageWrapper } from '/@/components/Page';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { message } from 'ant-design-vue';
  import { IconEnum } from '@/enums/appEnum';
  import { getTreeData, getIndexList, deleteIndex, saveData } from './videofirm';
  import { useI18n } from '@/hooks/web/useI18n';
  import { useMessage } from '@/hooks/web/useMessage';

  const currentEditNodeRef = ref();
  const currentEditKeyRef = ref('');

  const saveItem = ref<boolean>(false);
  const { t } = useI18n();
  const { createConfirm } = useMessage();
  const formState = reactive({});
  // 查询信息
  const searchInfo = reactive<any>({ folderId: 'test' });
  // 左侧树数据
  const treeData = ref<TreeItem[]>([]);
  // 被选择的树节点

  const loadingFlag = ref<boolean>(false);
  const checkedKeys = ref<Array<string | number>>([]);

  const indexColumn: BasicColumn[] = [
    {
      title: '厂商名称',
      dataIndex: 'name',
      width: 80,
      editRow: true,
      resizable: true,
      sorter: true,
      headAlign: 'center',
      align: 'left',
      editRule: async (text, record) => {
        if (!text) {
          return '不能为空';
        }
      },
      editComponent: 'Input',
    },
    {
      title: '厂商类型',
      dataIndex: 'firmType',
      width: 100,
      editRow: true,
      resizable: true,
      sorter: true,
      editComponent: 'DictSelectBox',
      editComponentProps: {
        type: 'videoFirmType',
      },
      editRule: async (text, record) => {
        if (!text) {
          return '不能为空';
        }
      },
    },
    {
      title: '用户名',
      dataIndex: 'userCode',
      width: 80,
      editRow: true,
      resizable: true,
      sorter: true,
      headAlign: 'center',
      align: 'left',
      editRule: async (text, record) => {
        if (!text) {
          return '不能为空';
        }
      },
      editComponent: 'Input',
    },
    {
      title: '密码',
      dataIndex: 'password',
      width: 80,
      editRow: true,
      resizable: true,
      sorter: true,
      headAlign: 'center',
      align: 'left',
      editRule: async (text, record) => {
        if (!text) {
          return '不能为空';
        }
      },
      editComponent: 'Input',
    },
    {
      title: 'appKey',
      dataIndex: 'appKey',
      width: 80,
      editRow: true,
      resizable: true,
      sorter: true,
      headAlign: 'center',
      align: 'left',
      editRule: async (text, record) => {
        if (!text) {
          return '不能为空';
        }
      },
      editComponent: 'Input',
    },
    {
      title: '秘钥',
      dataIndex: 'secret',
      width: 80,
      editRow: true,
      resizable: true,
      sorter: true,
      headAlign: 'center',
      align: 'left',
      editRule: async (text, record) => {
        if (!text) {
          return '不能为空';
        }
      },
      editComponent: 'Input',
    },
  ];

  const treeRef = ref();
  const hasSelected = computed(() => checkedKeys.value.length > 0);
  const indexList = ref<Array<any>>([]);
  const getIndexListByParam = async () => {
    currentEditKeyRef.value = '';
    currentEditNodeRef.value = null;
    let list = await getIndexList(searchInfo);
    indexList.value = list.result;
    return indexList.value;
  };

  const searchForm: FormProps = {
    baseColProps: { span: 8 },
    labelWidth: 120,
    showAdvancedButton: false,
    schemas: [
      {
        label: '厂商名称',
        field: 'name',
        component: 'Input',
      },
    ],
  };

  const [registerTable, { reload, getPaginationRef, getDataSource: getItemDataSource }] = useTable({
    title: '视频厂商',
    api: async () => {
      const paginationRef = getPaginationRef() as any;
      searchInfo.pageNo = paginationRef.current;
      searchInfo.pageSize = paginationRef.pageSize;
      let dataObj = await getIndexListByParam();
      return dataObj;
    },
    columns: indexColumn,
    useSearchForm: true,
    showIndexColumn: false,
    formConfig: searchForm,
    showTableSetting: true,
    resizeHeightOffset: 33,
    pagination: true,
    canResize: true,
    bordered: true,
    rowKey: 'id',
    actionColumn: {
      width: 80,
      title: '操作',
      dataIndex: 'action',
    },
    rowSelection: {
      type: 'checkbox',
      selectedRowKeys: checkedKeys,
      onSelect: onSelect,
      onSelectAll: onSelectAll,
    },
    handleSearchInfoFn(info) {
      searchInfo.name = info.name;
      return info;
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        searchInfo.sortField = sortInfo.field;
        searchInfo.sortOrder = sortInfo.order.replace('end', '');
      } else {
        searchInfo.sortField = null;
        searchInfo.sortOrder = null;
      }
    },
  });

  function onSelect(record, selected) {
    if (selected) {
      checkedKeys.value = [...checkedKeys.value, record.id];
    } else {
      checkedKeys.value = checkedKeys.value.filter((id) => id !== record.id);
    }
  }
  function onSelectAll(selected, selectedRows, changeRows) {
    const changeIds = changeRows.map((item) => item.id);
    if (selected) {
      checkedKeys.value = [...checkedKeys.value, ...changeIds];
    } else {
      checkedKeys.value = checkedKeys.value.filter((id) => {
        return !changeIds.includes(id);
      });
    }
  }
  // 查询左侧树数据
  async function queryTreeList() {
    const typeList = await getTreeData({});
    treeData.value = typeList.result as unknown[] as TreeItem[];
  }

  // 添加视频厂商
  function handleCreateIndex() {
    if (currentEditNodeRef.value != undefined) {
      message.error('请先完成编辑。');
      return false;
    }
    const data = getItemDataSource();
    // data.splice(0, data.length);
    const addRow: EditRecordRow = {
      editable: true,
      isNew: true,
      key: `${Date.now()}`,
    };
    if (data.length == searchInfo.pageSize) {
      data.splice(-1);
    }
    data.unshift(addRow);

    currentEditKeyRef.value = handleRecordKey(addRow);
    currentEditNodeRef.value = addRow;
  }

  function checkSelectData() {
    if (checkedKeys.value.length == 0) {
      message.error('请选择数据');
      return false;
    }
    return true;
  }

  function getWiringAction(record: EditRecordRow) {
    if (!record.editable) {
      return [
        {
          label: '修改',
          icon: 'clarity:bullet-list-line',
          disabled: handleCellDisabled(record), // record里面不存在字段‘key’，导致异常,
          onClick: handleEdit.bind(null, record),
        },
      ];
    }
    return [
      {
        label: '保存',
        onClick: handleSave.bind(null, record),
      },
      {
        label: '取消',
        popConfirm: {
          title: '是否取消修改',
          confirm: handleUnSave.bind(null, record),
        },
      },
    ];
  }

  function handleEdit(record: EditRecordRow) {
    currentEditKeyRef.value = handleRecordKey(record);
    currentEditNodeRef.value = record;
    record.onEdit?.(true);
  }

  const handleRecordKey = (record) => {
    if (Reflect.has(record, 'id')) {
      return Reflect.get(record, 'id');
    } else {
      return '';
    }
  };

  const handleCellDisabled = (record) => {
    const result = currentEditKeyRef.value
      ? currentEditKeyRef.value !== handleRecordKey(record)
      : false;
    return result;
  };
  // 行修改后保存
  async function handleSave(record: EditRecordRow) {
    saveItem.value = true;
    const valid = await record.onValid?.();
    if (valid) {
      saveData(record).then(() => {
        currentEditKeyRef.value = '';
        currentEditNodeRef.value = null;
        message.success('已修改');
        reload();
        saveItem.value = false;
      });
    }
  }

  // 行修改取消
  function handleUnSave(record: EditRecordRow) {
    currentEditKeyRef.value = '';
    currentEditNodeRef.value = null;
    saveItem.value = false;
    record.onEdit?.(false, false);
    reload();
  }

  // 删除视频厂商
  function handleDeleteIndex() {
    if (!checkSelectData()) {
      return;
    }
    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中记录？',
      async onOk() {
        deleteIndex('?ids=' + checkedKeys.value).then(() => {
          message.success('已删除');
          currentEditKeyRef.value = '';
          currentEditNodeRef.value = null;
          checkedKeys.value = [];
          reload();
        });
      },
    });
  }

  // 启动执行代码
  onMounted(() => {
    queryTreeList();
  });
</script>
<style lang="less" scoped>
  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings .anticon-setting) {
      margin-right: 0;
    }
  }
</style>
