import { defHttp } from '/@/utils/http/axios';
import { reactive } from 'vue';

enum Api {
  getTreeTypeUrl = '/baseset/common/list-orgStationTree',
  listIdexUrl = '/baseset/videoFirm/page',
  getIndexUrl = '/baseset/videoFirm/get',
  createIndexUrl = '/baseset/videoFirm/create',
  updateIndexUrl = '/baseset/videoFirm/update',
  deleteIndexUrl = '/baseset/videoFirm/delete',
}

// 组织树数据
export const getTreeData = (params) =>
  defHttp.post(
    {
      url: Api.getTreeTypeUrl,
      params,
    },
    {},
  );

// 获取视频厂商设置列表
export const getIndexList = (params) =>
  defHttp.get(
    {
      url: Api.listIdexUrl,
      params,
    },
    {},
  );

// 新增视频厂商设置
export const insertIndex = (params) =>
  defHttp.post(
    {
      url: Api.createIndexUrl,
      params,
    },
    {},
  );

// 修改视频厂商设置
export const updateIndex = (params) =>
  defHttp.put(
    {
      url: Api.updateIndexUrl,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return insertIndex(params);
  }
  return updateIndex(params);
};

// 删除视频厂商设置
export const deleteIndex = (params) =>
  defHttp.delete(
    {
      url: Api.deleteIndexUrl,
      params,
    },
    {},
  );

export const detailFormState = reactive({ spinning: false });

export const formIndexState = reactive({
  id: '',
  createTime: '',
});
